import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { Menu, Dropdown, Icon, Button, Card, Row, message, Col } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import {
  getClasss,
  deleteClass,
  exportStudents,
  exportStudentBackgrounds,
  exportPaperMissing,
  exportFullPaper,
  exportTrainingScoreTemplatePaper,
} from 'services/class';

import FormSearchClasses from './FormSearchClasses';

class ListClassesPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
    printing: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteClass(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  handlePrinting = async (classId, className, e) => {
    this.setState({ printing: { ...this.state.printing, [classId]: true } });
    const { key } = e;
    try {
      switch (key) {
        case 'student':
          await exportStudents(classId, className);
          break;
        case 'student-background':
          await exportStudentBackgrounds(classId, className);
          break;
        case 'paper-missing':
          await exportPaperMissing(classId, className);
          break;
        case 'full-paper':
          await exportFullPaper(classId, className);
          break;
        case 'training':
          await exportTrainingScoreTemplatePaper(classId, className);
          break;
        default:
      }
    } finally {
      this.setState({ printing: { ...this.state.printing, [classId]: false } });
    }
  };

  printMenu = (classId, className) => (
    <Menu onClick={e => this.handlePrinting(classId, className, e)}>
      <Menu.Item key="student">
        <Icon type="team" />
        Sinh viên
      </Menu.Item>
      <Menu.Item key="student-background">
        <Icon type="container" />
        Lý lịch sinh viên
      </Menu.Item>
      <Menu.Item key="paper-missing">
        <Icon type="file" />
        Thiếu giấy tờ
      </Menu.Item>
      <Menu.Item key="full-paper">
        <Icon type="file-zip" />
        Đã nộp giấy tờ
      </Menu.Item>
      <Menu.Item key="training">
        <Icon type="rise" />
        Rèn luyện
      </Menu.Item>
    </Menu>
  );

  columns = [
    {
      title: 'Tên',
      dataIndex: 'label',
      key: 'label',
      width: '10%',
    },
    {
      title: 'Số SV',
      dataIndex: 'noOfStudent',
      key: 'noOfStudent',
      width: '5%',
    },
    {
      title: 'Niên khóa',
      dataIndex: 'courseYearLabel',
      key: 'courseYearLabel',
      width: '10%',
    },
    {
      title: 'Tên khoa',
      dataIndex: 'facultyName',
      key: 'facultyName',
      width: '20%',
    },
    {
      title: 'CN',
      dataIndex: 'studyFieldName',
      key: 'studyFieldName',
      width: '20%',
    },
    {
      title: 'Số lượng đã phân lớp',
      dataIndex: 'noOfEnrolledStudent',
      key: 'noOfEnrolledStudent',
      width: '15%',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      key: 'operation',
      width: '20%',
      render: (text, record) => (
        <Fragment>
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.QUAN_LY_LOP__DANH_SACH_LOP_HOC__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
          <Dropdown
            overlay={this.printMenu(record.id, record.label)}
            disabled={this.state.printing[record.id]}
          >
            <Button type="default" className="btnAction">
              <Icon type={this.state.printing[record.id] ? 'loading' : 'export'} />
              In danh sách
              <Icon type="down" />
            </Button>
          </Dropdown>
        </Fragment>
      ),
    },
  ];

  render() {
    const { reload, paramSearch } = this.state;

    return (
      <Card title="DS lớp học">
        <CartHeadAction>
          <Link to={routeConstant.QUAN_LY_LOP__DANH_SACH_LOP_HOC__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchClasses />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getClasss}
              paramSearch={paramSearch}
              columns={this.columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListClassesPage />
  </Page>
);



